import { useEffect, useState } from "react";
import globals from "../../../util/global";
import jwtAxios from "../../../util/JWTaxios";
import "./getAllCompanies.css";
import notify from './../../../util/notify';
import { useNavigate } from "react-router-dom";
import SingleCompany from "../../company/singleCompany/singleCompany";

function GetAllCompanies(): JSX.Element {
    const navigate = useNavigate();

    const [companies, setCompanies]= useState([]);
    useEffect(()=>{
        jwtAxios.get(globals.urls.getAllCompanies)
        .then(response=>{
           // setCompanies(response.data);
            console.log(response);
        })
        .catch(err=>{
            notify.error("You must login first")
            navigate("/login");
        })
    },[]);
    return (
        <div className="getAllCompanies">
			<h1>All companies</h1><hr/>
            {companies.map(item=> <SingleCompany id={0} name={""} email={""} />)}
        </div>
    );
}

export default GetAllCompanies;
